
.Admin {
    position: relative;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 50%; 
    height: 400px; 
    box-sizing: border-box;
    float: inline-start;
}
.Admin button {
    margin: auto;
}
  
  .projectItem2 img,
  .projectItem2 video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

.projectItem2 h2 {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
    padding: 8px;
    box-sizing: border-box;
    opacity: 0; 
    transition: opacity 0.3s;
    }

.projectItem2:hover h2 {
    opacity: 1; 
    }

.projectItem2 h3 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
     padding: 8px;
    box-sizing: border-box;
    opacity: 0; 
    transition: opacity 0.3s;
    color: #c7146a;
    }

.projectItem2:hover h3 {
    opacity: 1; 
    }
      
.projectItem2:hover .img-container2::before {
    background-color: rgba(255, 255, 255, 0.8);
  }
h2.ourPortfolio {
  text-align: center;
  font-size: 30px;
  margin-bottom: 1px;
  font-family: 'Din Light';
  font-style: normal;
  font-weight: lighter;
}

p.line {
  color:#c7146a;
  font-size: 50px;
  margin-top: 0;
  margin-bottom: 10px;
}

@media only screen and (max-width: 768px){
  .projectItem2 {
    position: relative;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%; 
    height: 400px; 
    box-sizing: border-box;
    float: inline-start;
  }
}