.about-section {
    width: 100%;
    height: 600px;
    display: flex;
  }
  
  .about-section img {
    flex: 1; 
    width: 50%; 
    height: 100%;
    object-fit: cover; 
  }
  
  .aboutText {
    flex: 1; 
    width: 50%;
    background-color: #c7146a; 
    padding: 50px; 
    color: #ffffff; 
  }
  .aboutText h2{
    text-align: center;
    font-family: 'Din Light';
  font-style: normal;
  font-weight: lighter;
  }
  p.line1 {
    color:#ff8800;
    font-size: 50px;
    margin-top: 0;
    margin-bottom: 10px;
  }
  p.text {
    text-align: justify;
  }
  @media only screen and (max-width: 780px) {
    .about-section {
      width: 100%;
      height: auto;
      display: inline;
    }
    .about-section img {
      width: 100%; 
    }
    .aboutText {
      width: 100%;
      padding: 20px; }
  }