@import url('https://fonts.googleapis.com/css?family=Bree+Serif');

html, body, #root {
    margin:0px;
    height:100%;
    font-family: 'Din Light';
    font-style: normal;
    font-weight: lighter;
    font-size: 18px;
    text-align: justify;
    background-color: white;
    color:#767676 ;
}
.h2 {
    font-size: 30px;
}
@font-face {
    font-family: "AdriannaExtended-DemiBold";
    src: local("AdriannaExtended-DemiBold"),
     url("./fonts/AdriannaExtended-DemiBold.ttf") format("truetype");
   }
@font-face {
    font-family: "Kiro Light";
    src: local("Kiro Light"),
     url("./fonts/KiroLight.otf") format("opentype");
   }
@font-face {
    font-family: "Din Light";
    src: local("Din Light"),
     url("./fonts/DinLight.ttf") format("truetype");
   }