.services h2,p, h3{
    text-align: center;
    font-family: 'Din Light';
    font-style: normal;
    font-weight: lighter;
}

.services-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .services-list {
    flex: 0 0 33%; 
    margin-bottom: 20px; 
    padding-right: 4%;
    padding-left: 4%;
    box-sizing: border-box;
  }
  
  .services-list h4,
  .services-list ul,
  .services-list p {
    text-align: justify;
  }

  .services-list h4 {
    color: #c7146a;
  }

  .services-list img {
    width: 50%;
    display: block;
    margin: 0 auto;
    height: auto;
    max-width: 100%;
  }
  p.line {
    color:#c7146a;
    font-size: 50px;
    margin-top: 0;
    margin-bottom: 10px;
    text-align: center;
  }
  @media only screen and (max-width: 780px) {
    .services-container {
      display: block;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }