.contact-container h2 {
  text-align: center;
  font-family: 'Din Light';
  font-style: normal;
  font-weight: lighter;
}

.contact-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.contact-elements {
  flex: 0 0 45%; 
  margin-bottom: 20px; 
  margin-top: 100px;
  box-sizing: border-box;
}

.contact-elements-qr {
  flex: 0 0 20%; 
  margin-top: 200px;
  margin-bottom: 20px; 
  box-sizing: border-box;
}

.contact-elements-form {
  flex: 0 0 35%; 
  margin-bottom: 20px; 
  box-sizing: border-box;
  background-color: #c7146a;
  color: azure;
}

.send-button {
  background-color: #ff8800;
  font-family: 'Kiro Light';
  font-style: normal;
  color: #ffffff;
  border: 0;
  width: 70%;
  height: 40px;
  font-size: 18px;
}
.send-button:hover {
background-color: #f6bb22;
}
p.line {
color:#c7146a;
font-family: 'Din Light';
  font-style: normal;
  font-weight: lighter;
font-size: 50px;
margin-top: 0;
margin-bottom: 10px;
}
.privatePolicy {
font-size: 12px;
text-align: justify;
padding:5% 15% 5% 15%;
}
.input-text input {
width: 70%;
height: 30px;
text-align: center;
}
.input-text1 input {
width: 70%;
height: 200px;
text-align: center;
}
.formDesign {
margin-top: 100px;
}