.team {
    padding: 50px;
    background-color: #fafafa;
}
.team h2{
    text-align: center;
    font-family: 'Din Light';
    font-style: normal;
    font-weight: lighter;
}

.team-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .team-member {
    flex: 0 0 30%; 
    margin-bottom: 20px; 
    box-sizing: border-box;
  }
  
  .team-member img {
    width: 60%;
    display: block;
    margin: 0 auto;
    height: auto;
    max-width: 100%;
    border-radius: 50%; 
  }
  
  .team-member h3,
  .team-member h4,
  .team-member p {
    text-align: center;
  }
  p.line {
    color:#c7146a;
    font-size: 50px;
    margin-top: 0;
    margin-bottom: 10px;
  }
  @media only screen and (max-width: 780px) {
    .team-container {
      display: block;

    }
  }