.Menu {
  list-style-type: none;
  height: 68px;
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  overflow: hidden;
  background-color: white;
}

/* .sticky-menu {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
} */

.Nav {
  float: left;
  display: block;
  color: #767676;
  font-family: 'Kiro Light';
  background-color: #ffffff;
  border-width: 0;
  text-align: center;
  padding: 12px 10px;
  text-decoration: none;
  font-size: 12px;
  margin-right: 6px;
  margin-top: 15px;
}
.Nav:hover {
  color:#ff8800;
}
.Logo img {
  float: left;
  max-height: 45px;
  max-width: 100%;
  margin-right: 20px;
  margin-left: 60px;
  margin-top: 20px;
}

.LanguageDropdown {
  margin-top: 25px;
  float: right;
}

.LanguageDropdown select {
  color: #767676;
  border: 0;
  font-family: 'Kiro Light';
  font-style: normal;
  font-weight: lighter;
  font-size: 12x;
  float: right;
}
.dropdown {
  display: none; 
  text-align: center;
  margin-top: 15px;
}
.NavLink {
  float: right;
}
@media only screen and (max-width: 1100px) {
  .Nav {
    float: none;
    width: 100%;
    text-align: center;
    margin: 0;
    display: none; 
  }

  .Nav:hover {
    color: #ff8800;
  }

  .LanguageDropdown {
    margin-top: 10px;
  }

  .LanguageDropdown select {
    float: none;
    width: 100%;
  }
  .dropdown {
    display: block;
    text-align: center;
    margin-top: 15px;
  }
  .dropdown-btn {
    display: block;
    text-align: center;
    padding: 14px 16px;
    font-size: 16px;
    background-color: #ffffff;
    color: #767676;
    border: 0;
    cursor: pointer;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #ffffff;
    z-index: 1;
    width: 100%;
  }
  .dropdown-content.show {
    display: block;
  }

  .dropdown-content a {
    color: #767676;
    padding: 12px 16px;
    display: block;
    text-align: left;
    text-decoration: none;
  }

  .dropdown-content a:hover {
    background-color: #f1f1f1;
  }

  .dropdown:hover .dropdown-content {
    display: block;
  }
  .Logo img {
    float: left;
    max-height: 40px;
    max-width: 80%;
    margin-right: 20px;
    margin-left: 40px;
    margin-top: 10px;
  }
}
