.projectItem {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 50%; 
  height: 400px; 
  box-sizing: border-box;
  float: inline-start;
}
  
.projectItem img,
.projectItem video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.projectItem h2 {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  padding: 8px;
  box-sizing: border-box;
  opacity: 0; 
  transition: opacity 0.3s;
}

.projectItem:hover h2 {
  opacity: 1; 
}
.video-container {
  position: relative;
}

.img-container {
  position: relative;
}

.video-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: background-color 0.3s;
}
      
.img-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: background-color 0.3s;
}
.projectItem:hover .video-container::before {
  background-color: rgba(255, 255, 255, 0.8);
}
      
.projectItem:hover .img-container::before {
  background-color: rgba(255, 255, 255, 0.8);
}
.projectItem h3 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  padding: 8px;
  box-sizing: border-box;
  opacity: 0; 
  transition: opacity 0.3s;
  color: #c7146a;
}

.projectItem:hover h3 {
  opacity: 1; 
}

h2.ourPortfolio {
  text-align: center;
  font-size: 30px;
  margin-bottom: 1px;
  font-family: 'Din Light';
  font-style: normal;
  font-weight: lighter;
}

p.line {
  color:#c7146a;
  font-size: 50px;
  margin-top: 0;
  margin-bottom: 10px;
}

@media only screen and (max-width: 768px){
  .projectItem {
    position: relative;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%; 
    height: 400px; 
    box-sizing: border-box;
    float: inline-start;
  }
}