.parallaxApproach {
    height: 500px;
}

.approach {
    padding: 50px;
    text-align: justify;
}
.approach h2{
    text-align: center;
    font-family: 'Din Light';
    font-style: normal;
    font-weight: lighter;
}
p.line {
    color:#c7146a;
    font-size: 50px;
    margin-top: 0;
    margin-bottom: 10px;
  }
p.text {
    text-align: justify;
    padding-right: 20%;
    padding-left: 20%;
  }
p.subtitle {
    padding-left: 20%;
    padding-right: 20%;
    margin-bottom: 50px;
    margin-top: 0;
}
@media only screen and (max-width: 780px) {
    p.text {
        text-align: justify;
        padding-right: 5%;
        padding-left: 5%;
      }
}